<template>
  <div id="caic-sel-type-picker">
    <van-field v-model="type"
               :label="title"
               is-link
               :readOnly="true"
               @click="selectType">
    </van-field>
    <van-popup v-model="selTypePicker"
               position="bottom">
      <van-picker :columns='typeList'
                  show-toolbar
                  @cancel='selTypeCancel'
                  @confirm='selTypeSure'>
      </van-picker>
    </van-popup>
  </div>
</template>
<script>
/**
 * ===============done=======================
 * 通用的底部弹出层列表
 * value 实现双向绑定的必要值
 * title(String)输入框的标题
 * typeList(Array)选择项
 * @getMsg() (Function) 返回所选项的值和因
 * ==============doing=======================
 *
 * ==============hope=========================
 * 测试
 */
import Vue from 'vue'
import {
  Picker,
  Popup,
  Field,
} from 'vant'
Vue.use(Picker).use(Popup).use(Field)
export default {
  name: 'caic-credentials-picker',
  props: {
    defaultValue: {
      type: Object
    },
    title: {
      type: String,
      required: true
    },
    typeList: {
      type: Array,
      required: true
    }

  },
  data () {
    return {
      type: null,
      selTypePicker: false,
      test: null
    }
  },
  created () {
    if (this.defaultValue.value !== null && this.defaultValue.value !== undefined && this.defaultValue.value !== '' && this.defaultValue.index !== null && this.defaultValue.index !== undefined && this.defaultValue.index !== '') {
      this.type = this.defaultValue.value
      this.returnMsg(this.defaultValue.value, this.defaultValue.index)
    }
  },
  methods: {
    returnMsg: function (value, index) {
      this.$emit('getMsg',
        {
          value: value,
          index: index
        }
      )
    },
    selectType: function () {
      this.selTypePicker = true
    },
    selTypeCancel: function () {
      this.selTypePicker = false
    },
    selTypeSure: function (value, index) {
      this.selTypePicker = false
      this.type = value
      this.returnMsg(value, index)
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../css/caic.styl'

#caic-sel-type-picker
  .van-cell.van-cell--clickable.van-field
    changeFontSize(14px)
</style>



